import React, { Component } from 'react';
import './App.css';
import { Route } from 'react-router-dom';
import Home from './pages/Home';

class App extends Component {

  render() {
    return (
      <div>
        <Route exact path={`/`} render={ (routerProps) => <Home routerProps={routerProps} />} />
      </div>
    );
  }
}

export default App;
